import React, {
  ReactElement,
  RefObject,
  useCallback,
  useImperativeHandle,
  useMemo,
  useRef,
} from 'react';
import {
  ActionSheetIOS,
  ActivityIndicator,
  Dimensions,
  FlatList,
  LayoutChangeEvent,
  Linking,
  Platform,
} from 'react-native';
import {iosRefreshGallerySelection} from '@react-native-camera-roll/camera-roll';
import Row from './Row';
import {styled,useTheme} from '@true/core';
import {BottomSheetFlatList} from '@gorhom/bottom-sheet';
import {Fonts} from '@true/core';
import {ImageDTO, useGallery} from '@/components/CameraRollPicker/useGallery';
import {useSharedValue} from 'react-native-reanimated';
import {memoForwardRef} from '@true/core';
import {useGalleryPermission} from '@/components/CameraRollPicker/useGalleryPermission';

enum CameraRollAssetType {
  Photos = 'Photos',
  Videos = 'Videos',
  All = 'All',
}

interface CameraRollPickerProps {
  initialNumToRender?: number;
  groupTypes?: any;
  assetType?: CameraRollAssetType;
  selectSingleItem?: boolean;
  emptyText?: string;
  loader?: ReactElement;
  groupName?: string;
  selected?: ImageDTO[];
  onSelectImage?: (image: ImageDTO) => void;
  onSelectImages?: (images: ImageDTO[]) => void;
  isMarked?: boolean;
  isBottomSheet: boolean;
}

export interface CameraRollPickerRef {
  onImagePress: (image: ImageDTO) => void;
}

export const CameraRollPicker = memoForwardRef(
  (props: CameraRollPickerProps, ref: RefObject<CameraRollPickerRef>) => {
    const {
      emptyText,
      groupName,
      onSelectImage,
      onSelectImages,
      selected,
      groupTypes,
      isMarked,
      isBottomSheet,
    } = props;

    const {
      photos,
      loadNextPagePictures,
      hasNextPage,
      isLoadingNextPage,
      isLimited,
    } = useGallery({
      pageSize: 24,
      // @ts-ignore
      groupName,
      groupTypes,
    });
    const itemSize = useSharedValue(0);
    const selectedItemList = useRef(selected);

    const CustomFlatList = useMemo(() => {
      return isBottomSheet ? BottomSheetFlatList : FlatList;
    }, [isBottomSheet]);

    const theme = useTheme();

    const {readPermissionStatus, refreshPermissions, isLoading} =
      useGalleryPermission({
        autoRequestPermission: true,
      });

    const isPermittedToAccessGallery = useMemo(
      () =>
        readPermissionStatus === 'granted' ||
        readPermissionStatus === 'limited',
      [readPermissionStatus],
    );

    const changePermission = useCallback(() => {
      ActionSheetIOS.showActionSheetWithOptions(
        {
          options: ['Cancel', 'Add more photos', 'Change setting permission'],
          cancelButtonIndex: 0,
          userInterfaceStyle: theme.name == 'dark' ? 'dark' : 'light',
        },
        buttonIndex => {
          if (buttonIndex === 0) {
            // cancel action
          } else if (buttonIndex === 1) {
            iosRefreshGallerySelection().then();
          } else if (buttonIndex === 2) {
            Linking.openSettings().then();
          }
        },
      );
    }, []);

    const onLayout = useCallback((e: LayoutChangeEvent) => {
      const _width =
        e.nativeEvent?.layout?.width || Dimensions.get('window').width;
      itemSize.value = (_width - 3) / 3;
    }, []);

    const onSelectItem = useCallback(
      (item: ImageDTO) => {
        onSelectImage && onSelectImage(item);

        if (selectedItemList.current) {
          const newList = [...selectedItemList.current].filter(_item => {
            if (Platform.OS === 'android') {
              return _item.fileName !== item.fileName;
            }
            return _item.url !== item.url;
          });

          if (newList.length === selectedItemList.current.length) {
            selectedItemList.current = [...selectedItemList.current, item];
          } else {
            selectedItemList.current = newList;
          }

          onSelectImages?.(selectedItemList.current);
        }
      },
      [onSelectImage, onSelectImages, selectedItemList],
    );

    useImperativeHandle(ref, () => ({
      onImagePress: onSelectItem,
    }));

    const renderRow = useCallback(
      ({item}: {item: ImageDTO}) => {
        // @ts-ignore
        const isSelected = selected.findIndex(_item => {
          if (Platform.OS === 'android') {
            return _item.fileName === item.fileName;
          }
          return _item.url === item.url;
        });
        return (
          <Row
            isMarked={isMarked}
            isSelected={isSelected}
            onSelectItem={onSelectItem}
            photo={item}
            imageSize={itemSize}
          />
        );
      },
      [selected, onSelectItem, itemSize],
    );

    const data: ImageDTO[] = useMemo(() => {
      return [{url: 'add-media', date: new Date(), isButton: true}].concat(
        // @ts-ignore
        photos,
      );
    }, [photos]);

    const ListFooter = useCallback(() => {
      if (hasNextPage && isLoadingNextPage) {
        return (
          <ActivityIndicator
            size="large"
            color={'#007AFF'}
            style={{
              alignSelf: 'center',
            }}
          />
        );
      }
      return null;
    }, [hasNextPage, isLoadingNextPage]);

    return (
      // @ts-ignore
      <SWrapper onLayout={onLayout}>
        {isLimited ? (
          <SLimitedPhotoWrapper onPress={changePermission} activeOpacity={0.9}>
            <STextEditLimitedPhotos>
              Edit Permission Photos{' '}
              <STextButton>Change Permission</STextButton>
            </STextEditLimitedPhotos>
          </SLimitedPhotoWrapper>
        ) : null}
        {photos ? (
          <CustomFlatList
            ListFooterComponent={ListFooter}
            renderItem={renderRow}
            keyExtractor={keyExtractor}
            data={data}
            // @ts-ignore
            extraData={photos || selected.length || itemSize}
            onEndReachedThreshold={0.01}
            onEndReached={
              photos?.length < 24 || !hasNextPage
                ? undefined
                : loadNextPagePictures
            }
            numColumns={3}
            ListEmptyComponent={
              <SEmptyWrapper>
                <SEmptyText>{emptyText}</SEmptyText>
              </SEmptyWrapper>
            }
          />
        ) : null}
      </SWrapper>
    );
  },
);

const SWrapper = styled.View`
  flex: 1;
  background-color: ${p => p.theme.backgroundColor};
`;

const SEmptyWrapper = styled.View`
  flex: 1;
  justify-content: center;
  background-color: ${p => p.theme.backgroundColor};
`;
const SEmptyText = styled.Text`
  color: ${p => p.theme.grey1};
  font-size: 16px;
  text-align: center;
`;

const keyExtractor = (item: ImageDTO) => {
  return 'camera-roll ' + item.url;
};

const SLimitedPhotoWrapper = styled.TouchableOpacity`
  background-color: rgba(240, 247, 255, 1);
  padding: 6px 8px;
  border: 1px solid rgba(224, 224, 224, 1);
  margin-top: -5px;
`;

const STextEditLimitedPhotos = styled.Text`
  font-family: ${Fonts.Regular};
  
  font-size: 13px;
  line-height: 20px;
  color: #242424;
`;

const STextButton = styled.Text`
  font-family: ${Fonts.Bold};
  
  font-size: 13px;
  line-height: 20px;
  color: ${p => p.theme.primaryColor};
  text-decoration: underline;
  text-decoration-color: ${p => p.theme.primaryColor};
`;
